.App {
  text-align: center;
  overflow: hidden;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.Link-logo{
  height: 8vmin;
  pointer-events: none;
  padding-right: 7vmin;
  padding: 2%;
}

.App-header {
  background-image: url("./Pattern.png");
  background-color: #0E4D98;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: black;
  font-family: Arial, Helvetica, sans-serif;
}

h4 {
  padding-top: 1vmin;
}

.box{
  background-color: rgba(255, 255, 255, 0.9);
  border-radius: 25px;
  padding-left: 2vmin;
  padding-right: 2vmin;
}

.links {
  flex-flow: row;
  flex-wrap: wrap;
}